<template>
  <div class="foodOrderEdit">
    <foodHeaderNav :title="dishName" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="body">
      <div class="edit-list">
        <div class="edit-item" @click="orderReturnFun()">
          <span>退菜</span>
          <i class="iconfont icon-jiantou1"></i>
        </div>
        <!-- <div class="edit-item">
          <span>退菜</span>
          <i class="iconfont icon-jiantou1"></i>
        </div>
        <div class="edit-item">
          <span>退菜</span>
          <i class="iconfont icon-jiantou1"></i>
        </div>-->
      </div>
    </div>
    <foodOrderReturn
      v-if="isShowOrderReturn"
      @hideRetunPage="hideRetunPage($event)"
      :selectedMenu="selectedMenu"
      :orderNum="orderNum"
      :length="length"
    ></foodOrderReturn>
  </div>
</template>
<script>
import foodHeaderNav from "../common/foodHeaderNav";
import foodOrderReturn from "../common/foodOrderReturn";
import { mapState, mapGetters } from "vuex";
export default {
  components: {
    foodHeaderNav,
    foodOrderReturn
  },
  data() {
    return {
      isShowOrderReturnBtn: false,
      isShowOrderReturn: false,
      dishName: "",
      selectedMenu: "",
      length: 0,
      orderNum: null
    };
  },
  computed: {
    ...mapState({
      orderEdit: state => state.food.orderEdit
    })
  },
  created() {
    console.log("this.orderEdit:", this.orderEdit);
    this.orderNum = this.orderEdit.accnt;
    this.selectedMenu = this.orderEdit.pluInfos[this.orderEdit.index];
    this.length = this.orderEdit.pluInfos.length;
    this.dishName = this.orderEdit.pluInfos[this.orderEdit.index].descript;
    if (
      this.orderEdit.pluInfos[this.orderEdit.index].pluSta == 0 ||
      this.orderEdit.pluInfos[this.orderEdit.index].pluSta == 2
    ) {
      this.isShowOrderReturnBtn = false;
    } else {
      this.isShowOrderReturnBtn = true;
    }
  },
  mounted() {
    //console.log("selectedMenu:", this.selectedMenu);
  },
  methods: {
    gotoBack() {
      this.$router.push("/foodOrder");
    },
    orderReturnFun() {
      this.isShowOrderReturn = true;
    },
    hideRetunPage(val) {
      this.isShowOrderReturn = false;
    }
  }
};
</script>
<style lang="less" scoped>
.foodOrderEdit {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 1;
  background-color: #f4f5f7;

  .body {
    .edit-list {
      padding-left: 0.4rem;
      background-color: #fff;
      .edit-item {
        border-bottom: 1px solid #eeeeee;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-right: 0.4rem;
        font-size: 0.373rem;
        color: #000000;
        height: 1.253rem;
        line-height: 1.253rem;
      }
      .edit-item:last-child {
        border-bottom: none;
      }
    }
  }
}
</style>



