<template>
  <div class="foodOrderReturn">
    <foodHeaderNav title="退菜" eventShow="true" @hidePageFun="gotoBack()"></foodHeaderNav>
    <div class="quantity-container">
      <div class="quantity-content">
        <div class="quantity-text">退菜：</div>
        <!-- <div class="quantity-operating">
          <div class="cut-btn" @click="cutFun()">
            <i class="iconfont icon-jian"></i>
          </div>
          <span class="num">{{menu.number}}</span>
          <div class="add-btn icon-btn" @click="addFun()">
            <i class="iconfont icon-jia1"></i>
          </div>
        </div>-->
      </div>
    </div>
    <div class="reason">
      <div class="reason-list">
        <div
          class="reason-item"
          v-for="(item, index) in reasonArr"
          :key="index"
          @click="selectFun(index)"
        >
          <div class="reason-text">{{item.reason}}</div>
          <i class="iconfont icon-right" v-if="index==selectIndex"></i>
        </div>
      </div>
    </div>
    <div class="foot" @click="okFun()">确 定</div>
  </div>
</template>
<script>
import foodHeaderNav from "../common/foodHeaderNav";
import { Indicator, Toast, MessageBox } from "mint-ui";

export default {
  components: {
    foodHeaderNav
  },
  props: {
    selectedMenu: Object,
    length: Number,
    orderNum: String
  },
  data() {
    return {
      selectIndex: -1,
      reasonArr: [],
      menu: _.cloneDeep(this.selectedMenu),
      maxNum: this.selectedMenu.number
    };
  },
  mounted() {
    console.log("selectedMenu:", this.selectedMenu);
  },
  methods: {
    gotoBack() {
      this.$emit("hideRetunPage", "");
    },
    selectFun(index) {
      if (this.selectIndex == index) {
        this.selectIndex = -1;
      } else {
        this.selectIndex = index;
      }
    },
    cutFun() {
      if (this.menu.number > 1) {
        this.menu.number--;
      }
    },
    addFun() {
      if (this.menu.number < this.maxNum) {
        this.menu.number++;
      }
    },
    okFun() {
      console.log("this.menu:", this.menu);
      if (this.length == 1) {
        MessageBox.confirm("仅剩一个菜品，退菜成功即为取消订单")
          .then(action => {
            this.deleteMenu();
          })
          .catch(action => {});
      } else {
        this.deleteMenu();
      }
    },
    deleteMenu() {
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "updatePosDetail",
            detailId: this.menu.pluDetailId,
            accnt: this.orderNum
          },
          vv: 5
        })
        .then(res => {
          console.log("tuicai:", res);
          if (res.data.code == "0") {
            this.$router.push("/foodOrder");
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.foodOrderReturn {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  z-index: 9;
  background-color: #f4f5f7;

  .quantity-container {
    background-color: #fff;
    padding-left: 0.533rem;
    margin-bottom: 0.453rem;
    .quantity-content {
      display: flex;
      align-items: center;

      height: 1.253rem;
      border-bottom: 1px solid #eee;
      .quantity-text {
        font-size: 0.373rem;
        color: #a2a8bd;
      }
      .quantity-operating {
        width: 2.4rem;
        display: flex;
        align-items: center;
        .cut-btn {
          .iconfont {
            font-weight: bold;
            color: #ff7b02;
            font-size: 0.48rem;
          }
        }
        .num {
          font-size: 19.2px;
          color: #77a0ff;
          width: 0.933rem;
        }
        .add-btn {
          width: 0.507rem;
          height: 0.507rem;
          line-height: 0.507rem;
          border-radius: 2.667rem;
          background: #ff7b02;
          .iconfont {
            color: #fff;
            font-size: 0.32rem;
          }
        }
      }
    }
  }
  .reason {
    .reason-list {
      background: #ffffff;
      padding-left: 0.533rem;
      .reason-item:last-child {
        border: none;
      }
      .reason-item {
        border-bottom: 1px solid #eee;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 0.587rem;
        height: 1.253rem;

        .reason-text {
          font-size: 0.373rem;
          color: #000000;
        }
        .iconfont {
          color: #f89635;
        }
      }
    }
  }
  .foot {
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 1.2rem;
    line-height: 1.2rem;
    font-size: 0.427rem;
    color: #ffffff;
    background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
  }
}
</style>



